import MyTitle from "../../components/MyTitle.vue";
export default {
	components:{
		MyTitle
	},
	data(){
		return {
			// 选中状态
			currentIndexNav:0,
			// 导航数据
			navList:[],
			// 轮播图数据
			swiperList:[],
			// 视频数据
			videosList:[]
		}
	},
	methods:{
		// 导航栏点击时间
		activeNav(index){
			this.currentIndexNav = index;
		},
		// 获取导航数据
		getNavList(){
			uni.request({
				url:"https://easy-mock.com/mock/5c1dfd98e8bfa547414a5278/bili/navlist",
				data:{},
				header:{},
				success: res => {
					this.navList = res.data.data.navList;
				},
				fail: res => {
					console.log("请求失败");
				}
			})
		},
		// 获取轮播图数据
		getLunbo(){
			uni.request({
				url:'https://easy-mock.com/mock/5c1dfd98e8bfa547414a5278/bili/swiperList',
				data:{},
				header:{},
				success: res => {
					this.swiperList = res.data.data.swiperList;
				},
				fail: res => {
					console.log("请求失败");
				}
			})
		},
		// 获取视频列表数据
		getVideoList(){
			uni.request({
				url:'https://easy-mock.com/mock/5c1dfd98e8bfa547414a5278/bili/videosList',
				data:{},
				header:{},
				success: res => {
					this.videosList = res.data.data.videosList;
				},
				fail: res => {
					console.log("请求失败");
				}
			})
		},
		// 将视频id传到vuex中
		toDetail(index){
			this.$store.commit('getVideoId',index);
		}
	},
	onLoad(){
		this.getNavList();
		this.getLunbo();
		this.getVideoList();
	}
}
